.myLink {
  border-radius: 50%;
  outline: 1px solid #fff;
  padding: 12px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  background: none;
  position: relative;
  transition: 0.5s;
  svg {
    color: #fff;
  }
  &::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    border-radius: 50%;
    z-index: -1;
    transition: 0.5s;
    scale: 0;
    background-color: #3a3a3a;
  }
  &:hover {
    outline: 1px solid transparent;
    &::after {
      scale: 1;
    }
  }
}
@media (max-width: 900px) {
  .myLink {
    font-size: 20px;
  }
}

@media (max-width: 470px) {
  .myLink {
    font-size: 16px;
  }
}
